import React, { Component } from "react";
import Logo from "../../assets/pev.png";
import { Form, Container, GreenButton} from "./styles";

class Sensor extends Component {
  state = {
    sensor_id:"",
    volume:"",
    error: ""
  };

  handleSignUp = e => {
    alert("Eu vou registrar");
  };
  logoutClick(e){
    let url = '/logout'
    window.location.href = url;
  }


  render() {
    return (
      <Container>
        <div>
        <Form onSubmit={this.handleSignUp}  action='/sensor' method='post'>
          <img src={Logo} alt="PEV logo" />
          {this.state.error && <p>{this.state.error}</p>}
          <input
            type="number"
            placeholder="sensor_id"
            onChange={e => this.setState({ sensor_id: e.target.value })}
            name="sensor_id"
          />
          <input
            type="number"
            placeholder="Volume"
            onChange={e => this.setState({ volume: e.target.value })}
            name="volume"
          />
          <button type="submit">Registrar grátis</button>
        </Form>
        <hr />
          <GreenButton onClick={this.logoutClick}>
            Logout
          </GreenButton>
        </div>
        
      </Container>
    );
  }
}

export default Sensor;