import React, { Component } from 'react';

// Step 2 - Including the react-fusioncharts component
import ReactFC from 'react-fusioncharts';

// Step 3 - Including the fusioncharts library
import FusionCharts from 'fusioncharts';

// Step 4 - Including the chart type
import Widgets from 'fusioncharts/fusioncharts.widgets';

// Step 5 - Including the theme as fusion
import FusionTheme from 'fusioncharts/themes/fusioncharts.theme.fusion';
import { deepCopyOf } from 'react-fusioncharts/lib/utils/utils';

import {Container, GreenButton} from  './styles';
// Step 6 - Adding the chart and theme as dependency to the core fusioncharts
ReactFC.fcRoot(FusionCharts, Widgets, FusionTheme);

// Step 7 - Creating the JSON object to store the chart configurations
const chartConfigs = {
  type: 'angulargauge', // The gauge type
  width: '450', // Width of the gauge
  height: '250', // Height of the gauge
  dataFormat: 'json', // Data type
  dataSource: null,
};
class Dashboard extends Component {
    constructor(props) {
        super(props);
        this.state = {
          sensors: [],
          chart_data: null,
          charts:[],
        };
      }
      componentDidMount() {
        console.log("Getting sensors...")
        let url = '/sensor/all'
        const base_sensor_volume_url = '/sensor/volume/'
    
        fetch(url)
          .then(res => res.json())
          .then(sensorIDs => {
            Promise.all(sensorIDs.map(sid => this.getData(base_sensor_volume_url + sid))).then(chartDataList => {
              let index =0
              for(let chartData of chartDataList){
                chartConfigs['dataSource'] =chartData
                chartConfigs.key = index++
                this.state.charts.push(this.renderCharts(deepCopyOf(chartConfigs)))
              }
              this.setState({charts: this.state.charts})
            })
           
          })
          .catch(console.log);
    
      }
    
      getData(url) {
        return fetch(url)
          .then(res => res.json())
      }

      updateVolumeCharts(){
        let url = '/sensor/all'
        const base_sensor_volume_url = '/sensor/volume/'
    
        fetch(url)
          .then(res => res.json())
          .then(sensorIDs => {
            Promise.all(sensorIDs.map(sid => this.getData(base_sensor_volume_url + sid))).then(chartDataList => {
              let index =0
              for(let chartData of chartDataList){
                chartConfigs['dataSource'] =chartData
                chartConfigs.key = index++
                this.state.charts.push(this.renderCharts(deepCopyOf(chartConfigs)))
              }
              this.setState({charts: this.state.charts})
            })
           
          })
          .catch(console.log);
      }
    
      renderCharts(chartConfigs) {
        console.log(chartConfigs)
        return <ReactFC {...chartConfigs} />;
      }

      handleClick(e){
        e.preventDefault();
        let url = '/sensor/all'
        const base_sensor_volume_url = '/sensor/volume/'
    
        fetch(url)
          .then(res => res.json())
          .then(sensorIDs => {
            Promise.all(sensorIDs.map(sid => this.getData(base_sensor_volume_url + sid))).then(chartDataList => {
              let index =0
              for(let chartData of chartDataList){
                chartConfigs['dataSource'] =chartData
                chartConfigs.key = index++
                this.state.charts.push(this.renderCharts(deepCopyOf(chartConfigs)))
              }
              this.setState({charts: this.state.charts})
            })
           
          })
          .catch(console.log);
      }

      logoutClick(e){
        let url = '/logout'
        window.location.href = url;
      }

  render() {
    return (  
      <div>
        <Container> 
          <div>
            {this.state.charts}
          </div>
        </Container>
        <div>
          <GreenButton onClick={this.handleClick}>
            Atualizar
          </GreenButton>
          <GreenButton onClick={this.logoutClick}>
            Logout
          </GreenButton>
        </div>
       </div>
     );
  }
}

export default Dashboard;